footer {
    display: flex;
    align-items: center;
    background-color: var(--primary);
    color: var(--white);
    height: 4em;
    padding: 1em;

    >div {
        width: 100%;
        max-width: 1440px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    @media (max-width: 576px) {
        height: 6em;
        padding: 0.5em;

        >div {
            flex-wrap: wrap-reverse;
            justify-content: center;
            gap: 0.5em;
        }
    }

    .x-btn {
        display: flex;
        gap: 0.5em;
    }

    .x-controller {
        span {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid var(--border);
            padding: 0.4em 1em;
            border-radius: 1em;

            &:hover {
                border: 1px solid var(--primary);
            }

            &.active {
                background-color: var(--danger);

                &:hover {
                    background-color: var(--danger-hover);
                }
            }
        }
    }

    .x-code {
        border: 1px solid var(--border);
        padding: 0.4em 1em;
        border-radius: 1em;
        max-width: 240px;
        width: 240px;
        cursor: pointer;

        .spot-name {
            display: block;
            width: 200px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;

            &:hover {
                overflow: visible;
            }
        }

        &:hover {
            border: 1px solid var(--primary);
        }
    }
}