body {
    height: 100%;
    width: 100%;
    position: fixed;
}

#root {
    height: 100%;
}

main {
    height: calc(100% - 8em);

    @media (max-width: 576px) {
        height: calc(100% - 10em);
    }
}

button {
    &:hover {
        cursor: pointer;
    }
}

.modal {
    .overlay {
        position: fixed;
        top: 0;
        background-color: var(--grey-dim);
        height: 100%;
        width: 100vw;
    }

    .modal-content {
        height: 100%;
        width: 500px;
        background-color: var(--white);
        color: var(--grey);
        position: fixed;
        top: 0px;
        opacity: 1;

        .modal-body {
            height: calc(100% - 4em);
            padding: 1em;

            h1 {
                font-size: 1.3em;
                margin-bottom: .3em;
            }

            hr {
                margin: 1em 0 .7em;
                border-color: var(--grey);
                opacity: .2;
            }

            p {
                margin: .5em 0;
            }

            >button {
                background-color: var(--primary);
                border-radius: 2em;
                width: 100%;
                color: var(--white);
                padding: .5em;
                font-size: 1em;
                border: 3px solid var(--primary);
                margin: 1em 0 1em;

                &:active {
                    border: 3px solid var(--border-active);
                }

                &:focus {
                    border: 3px solid var(--border-active);
                }
            }

            .user-panel {
                padding: .7em 1.5em;
                background-color: var(--contact-back);
                border: 1px solid var(--contact-border);
                border-radius: .3em;
                color: var(--contact);
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-size: 1em;
                margin: .6em 0;

                >div {
                    display: flex;
                    align-items: center;
                    gap: .5em;
                }

                img {
                    width: 2em;
                    height: 2em;
                    object-fit: cover;
                    border-radius: 4px;
                }

                button {
                    background-color: var(--danger);
                    border-radius: 4px;
                    border: none;
                    padding: .7em 1em;
                    color: var(--white);

                    &:hover {
                        background-color: var(--danger-hover);
                    }
                }
            }

            select {
                width: 100%;
                padding: .3em .6em;
                font-size: 1em;
                margin-bottom: .5em;
                border-radius: .3em;
                color: var(--grey-dark);
            }
        }

        .modal-footer {
            height: 4em;
            border-top: 1px solid var(--grey);
            padding: .6em 1em;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            button {
                background-color: var(--green);
                border: 1px solid var(--green);
                color: var(--white);
                padding: .5em 1em;
                border-radius: 5px;
                font-size: 1em;
            }
        }

        @media (max-width: 576px) {
            width: 100vw;
        }
    }

    &.left {
        .overlay {
            left: -100vw;
            transition-property: opacity, left;
            transition-duration: 0, 0.3s;
        }

        .modal-content {
            left: -576px;
            transition-property: opacity, left;
            transition-duration: 0.25s, 0.3s;

            .user-group {
                overflow-y: auto;
                height: calc(100% - 21em);
            }
        }

        &.show {

            .modal-content,
            .overlay {
                left: 0;
                opacity: 1;
            }
        }
    }

    &.right {
        .overlay {
            right: -100vw;
            transition-property: opacity, right;
            transition-duration: 0, 0.3s;
        }

        .modal-content {
            right: -576px;
            transition-property: opacity, right;
            transition-duration: 0.25s, 0.3s;
        }

        &.show {

            .modal-content,
            .overlay {
                right: 0;
                opacity: 1;
            }
        }

        h1 {
            margin-bottom: 1.3em;
        }
    }

    &.top {
        z-index: 100;

        .overlay {
            top: -100%;
            left: 0;
            transition-property: opacity, top;
            transition-duration: 0, 0.3s;
        }

        .modal-content {
            top: -100%;
            transition-property: opacity, top;
            transition-duration: 0.25s, 0.3s;

            .user-group {
                overflow-y: auto;
                height: calc(100% - 8em);
            }
        }

        &.show {

            .modal-content,
            .overlay {
                top: 0;
                opacity: 1;
            }
        }

        h1 {
            margin-bottom: 1.3em;
        }
    }
}