nav {
    height: 4em;
    padding: 0.5em 1em;
    color: var(--white);
    display: flex;
    align-items: center;
    background-color: var(--primary);

    img {
        width: 3.5em;
        height: auto;
        vertical-align: middle;
        margin-right: 10px;

        &:hover {
            cursor: pointer;
        }
    }

    h1 {
        font-size: 1.3em;
        display: flex;
        align-items: center;
        gap: 0.5em;
    }
}